.featured {
  height: 100vh;
}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}
.featured--name {
  font-size: 60px;
  font-weight: bold;
  margin-top: -190px;
}
.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}
.featured--points,
.featured--year,
.featured--seasons { 
  display: inline-block;
  margin-right: 15px;
}
.featured--points {  
  color: #46d369;
}
.featured--description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}
.featured--buttons {
  margin-top: 15px;
  border: 0;
}
.featured--watchbutton, .featured--mylistbutton {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 12px 20px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 30px;
  opacity: 1;
}
.featured--watchbutton:hover,
.featured--mylistbutton:hover {
  opacity: 0.7;
  transition: all ease 0.2;
}
.featured--watchbutton {
  background-color: #FFF;
  color: #000;
  border: 2px solid #333;
}
.featured--mylistbutton {
  background-color: #333;
  color: #FFF;
  border: 2px solid rgb(63, 62, 62);
}
.featured--genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media (max-width:760px) {
  .featured {
    height: 90vh;
  }
  .featured--name {
    font-size: 40px;
    margin-top: 0px;
  }
  .featured--info {
    font-size: 16px;
  }
  .featured--horizontal {
    display: block;
  }
  .featured--description {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }
  
  .featured--buttons {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }
  .featured--watchbutton,
  .featured--mylistbutton {
    display: inline-block;
    font-size: 16px;
    opacity: 1;
  }
  .featured--genres {
    font-size: 16px;
  }
}